@import url(https://fonts.googleapis.com/css?family=Open+Sans&display=swap);
:root{font-size:16px;font-family:"Open Sans";--text-primary: #b6b6b6;--text-secondary: #ececec;--bg-primary: #23232e;--bg-secondary: #141418;--transition-speed: 600ms}html{margin:0;padding:0}body{margin:0;padding:0;color:black;background-color:white}body::-webkit-scrollbar{width:0.25rem}body::-webkit-scrollbar-track{background:#1e1e24}body::-webkit-scrollbar-thumb{background:#6649b8}.navbar{position:fixed;background-color:#23232e;background-color:var(--bg-primary);transition:width 200ms ease}.navbar:hover .navbar-nav .logo .nav-link svg{transform:rotate(-180deg)}.navbar .navbar-nav{list-style:none;padding:0;margin:0;display:flex;flex-direction:column;align-items:center}.navbar .navbar-nav .nav-item{width:100%}.navbar .navbar-nav .nav-item .logo{font-weight:bold;text-transform:uppercase;margin-bottom:1rem;text-align:center;color:#ececec;color:var(--text-secondary);background:#141418;background:var(--bg-secondary);letter-spacing:0.3ch;width:100%}.navbar .navbar-nav .nav-item .logo .link-text.logo-text{position:absolute;left:-999px;transition:600ms;transition:var(--transition-speed)}.navbar .navbar-nav .nav-item .nav-link{display:flex;align-items:center;height:5rem;color:#b6b6b6;color:var(--text-primary);text-decoration:none;-webkit-filter:grayscale(100%) opacity(0.7);filter:grayscale(100%) opacity(0.7);transition:600ms;transition:var(--transition-speed)}.navbar .navbar-nav .nav-item .nav-link:hover{-webkit-filter:grayscale(0%) opacity(1);filter:grayscale(0%) opacity(1);background:#141418;background:var(--bg-secondary);color:#ececec;color:var(--text-secondary)}.navbar .navbar-nav .nav-item .nav-link svg{max-width:2rem;margin:0 1.5rem}.navbar .navbar-nav .nav-item .nav-link .link-text{display:none;margin-left:1rem}.navbar .navbar-nav:last-child{margin-top:auto}.fa-primary{color:#ff77ee}.fa-secondary{color:#df49a6}.fa-primary,.fa-secondary{transition:600ms;transition:var(--transition-speed)}main{padding:1rem;margin-left:5rem}@media only screen and (max-width: 600px){.navbar{bottom:0;width:100vw;height:5rem}.navbar .navbar-nav{flex-direction:row}.navbar .navbar-nav .nav-item .nav-link{justify-content:center}.navbar .navbar-nav .logo{display:none}main{margin:0}}@media only screen and (min-width: 600px){.navbar{top:0;width:5rem;height:100vh}.navbar:hover{width:16rem}.navbar:hover .navbar-nav .nav-item .nav-link .link-text,.navbar:hover .navbar-nav .nav-item.logo .nav-link .link-text{display:block;transition:opacity 600ms;transition:opacity var(--transition-speed)}.navbar:hover .navbar-nav .nav-item.logo .nav-link .link-text.logo-text{left:0px}}

